import React, {useState, useEffect} from 'react';
import immer from "immer";





const DashboardChat = ({socket, activeLobby, messages, setMessages, message, setMessage, publicKey}) => {
    
    

    async function addMessage(){
        
        const payload = {
            content: message,
            to: activeLobby,
            sender: publicKey, 
            chatName: activeLobby,
            isChannel: true
        }
        console.log("send this message:", payload)
        socket.emit('send message', payload)
        setMessage('')
        
    }
    socket.on("new message", (newmsg) => {
        let newMessages = immer(messages, draft => {
            draft[activeLobby].push(newmsg)
        })
        setMessages(newMessages)
        
    }) 

    // socket.on('new message', (newmsg) => {
    //     let newMessages = immer(messages, draft => {
    //       draft[activeLobby].push(newmsg)
    //   })
    //   setMessages(newMessages)
    //     console.log("tried setting", Object.values(messages[activeLobby]))
    //   }) 

    //update messages in lobbyList
     

    return(
        <div id="dashboard-chat">
            <div id='current-lobby'>{activeLobby}</div>
            <div id="messages">
                {Object.values(messages[activeLobby]).map((msg, index)=> <div className='message' key={index}><h3>{msg.sender}</h3><p>{msg.content}</p></div>)}
                
                
                </div>

            <div id="message-form">
            
            <input 
            autoFocus 
            autoComplete='off'
            id="message-input" 
            type="text" 
            placeholder="message" 
            value={message} 
            onChange={(e)=>{setMessage(e.target.value)}} 
            onKeyDown={(e)=>{e.key === "Enter" && addMessage()}}
            
            />
           
            
            </div>
        </div>
    );


}

export default DashboardChat